import React from 'react';
import classnames from 'classnames';
import './PageBlock.scss';

type PageBlockProps = {
  className?: string;
  maxWidth?: string;
};

const PageBlock: React.FC<PageBlockProps> = (props) => {

  const classes = classnames('PageBlock', props.className);
  const innerStyles: React.CSSProperties = {};
  if (props.maxWidth) innerStyles.maxWidth = props.maxWidth;

  return (
    <div className={classes}>
      <div className="PageBlock__inner" style={innerStyles}>
        {props.children}
      </div>
    </div>
  );
};

export { PageBlock };
