import React from 'react';
import { useLocation } from 'react-router-dom';
import { CardGrid, PageBlock } from '_components';
import { ARTICLES } from 'articles';
import './MoreReading.scss';

type MoreReadingProps = {};

const MoreReading: React.FC<MoreReadingProps> = (props) => {

  const location = useLocation();

  // Filter the current article out, and select the first 3 articles
  const articles = Object.values(ARTICLES)
    .filter(item => item.path !== location.pathname)
    .slice(0, 3);

  return (
    <div className="MoreReading">
      <PageBlock maxWidth="1400px">
        <h1 className="MoreReading__title">Recent posts</h1>
        <CardGrid articles={articles} />
      </PageBlock>
    </div>
  );
};

export { MoreReading };
