import React from 'react';
import { Link } from 'react-router-dom';
import TimeAgo from 'react-timeago';
import { LazyImage } from '_components';
import { Article } from '_types';
import './Card.scss';

type CardProps = {
  article: Article;
};

const Card: React.FC<CardProps> = (props) => {

  return (
    <div className="Card">
      <Link className="Card__image u-anchor" to={props.article.path}>
        <LazyImage src={props.article.image} ratio={3/2} />
      </Link>
        <h3 className="Card__title">
          <Link className="u-anchor u-anchor--accentHover" to={props.article.path}>{props.article.title}</Link>
        </h3>
      <p className="Card__description">{props.article.description}</p>
      <p className="Card__timeAgo">
        <TimeAgo date={props.article.date} />
      </p>
    </div>
  );
};

export { Card };
