import React from 'react';
import classnames from 'classnames';
import { useLoadImage } from '_hooks';
import './LazyImage.scss';

type LazyImageProps = {
  src: string;
  previewSrc?: string;
  color?: string;
  ratio?: number;
  className?: string;
}

const LazyImage: React.FC<LazyImageProps> = (props) => {

  const { loaded, cached } = useLoadImage(props.src);

  const lazyImageStyles: React.CSSProperties = {};
  if (props.ratio) lazyImageStyles.paddingTop = `${100 / props.ratio}%`;
  if (props.color) lazyImageStyles.backgroundColor = props.color;

  const previewStyles: React.CSSProperties = {};
  if (props.previewSrc) previewStyles.backgroundImage = `url(${props.previewSrc})`;

  const imageStyles: React.CSSProperties = {};
  if (loaded) imageStyles.backgroundImage = `url(${props.src})`;

  const imageClasses = classnames('LazyImage__image', props.className, {
    'LazyImage__image--loaded': loaded,
    'LazyImage__image--cached': cached,
  });

  const lazyImageClasses = classnames('LazyImage', props.className);

  return (
    <div className={lazyImageClasses} style={lazyImageStyles}>
      {props.previewSrc &&
        <div className="LazyImage__preview" style={previewStyles}></div>
      }
      <div className={imageClasses} style={imageStyles}></div>
    </div>
  );
}

export { LazyImage };
