import React, { Suspense } from 'react';
import 'normalize.css';
import '_styles/index.scss';
import { Switch, Route, useLocation } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { useRecoilValue } from 'recoil';
import { analyticsEnabledState, siteHeaderVisibleState } from '_state';
import { Loading, TrackPage, ScrollToTop, SiteHeader } from '_components';
import { analyticsService } from '_services';
// import { useAnalyticsStore } from '_hooks';
import { ROUTES } from 'routes';
import './App.scss';
const App: React.FC = () => {

  const location = useLocation();
  const siteHeaderVisible = useRecoilValue(siteHeaderVisibleState);
  const analyticsEnabled = useRecoilValue(analyticsEnabledState);

  // Enable analytics. At a later date, can add method to disable analytics based on a cookie popup.
  analyticsEnabled ? analyticsService.enable() : analyticsService.disable();

  return (
    <div className="App">
      <ScrollToTop delay={210} />
      <TrackPage />
      <SiteHeader visible={siteHeaderVisible} />
      <TransitionGroup>
        <CSSTransition key={location.pathname} classNames="fade" timeout={{ enter: 1000, exit: 200 }}>
          <Suspense fallback={<Loading />}>
            <Switch location={location}>
              {Object.values(ROUTES).map((item, idx) =>
                // @ts-ignore
                <Route {...item} key={idx} />
              )}
            </Switch>
          </Suspense>
        </CSSTransition>
      </TransitionGroup>
    </div>
  );
}

export { App };
