import React, { forwardRef } from 'react';
import { PageBlock } from './PageBlock';
import './SkillModule.scss';

type SkillModuleProps = React.ComponentPropsWithoutRef<'div'> & {
  maxWidth?: string;
  title: string;
  icon: JSX.Element;
};

const SkillModule = forwardRef<HTMLDivElement, SkillModuleProps>((props, ref) => {

  return (
    <div className="SkillModule" ref={ref}>
      <PageBlock maxWidth={props.maxWidth}>
        <div className="SkillModule__container">
          <div className="SkillModule__icon">{props.icon}</div>
          <h1 className="SkillModule__title">{props.title}</h1>
          {props.children}
        </div>
      </PageBlock>
    </div>
  );
});

export { SkillModule };
